import { Card, Button } from "react-bootstrap";
import styles from "./FavoriteItem.module.css";

const FavoriteItem = ({ detail }) => {
  return (
    <div className={styles.cardPosition}>
      {detail !== 0 ? (
        <Card>
          <img className={styles.imageSize} src={detail.src} />
          <Card.Body className={styles.textPosition}>
            <Card.Title>{detail.title}</Card.Title>
            <Card.Text>You Liked this picture on</Card.Text>
            <Button variant="danger">Remove</Button>
          </Card.Body>
        </Card>
      ) : (
        <Card.Text>You don't have favorite item yet</Card.Text>
      )}
    </div>
  );
};

export default FavoriteItem;
